import {
  FormItem,
  RadioFormItem,
  FormItemContainer,
  ItemName,
} from "../../components/LoginRegisterForm/LoginRegisterFormItem";
import {
  Wrapper,
  Container,
  Title,
  FormContainer,
  Btn,
  ErrorMessage,
} from "../../components/LoginRegisterForm/LoginRegisterFormWrapperStyle";
import useLogin from "../../components/LoginRegisterForm/useLogin";

function LoginPage() {
  const { handleLoginSubmit, loginData, handleLoginDataChange, errorMessage } =
    useLogin();

  return (
    <Wrapper>
      <Container>
        <Title></Title>
        <FormContainer onSubmit={handleLoginSubmit}>
          {errorMessage && <ErrorMessage>{errorMessage}</ErrorMessage>}
          <FormItem
            itemName="Email"
            id="email"
            value={loginData.email}
            type="email"
            name="email"
            handleChange={handleLoginDataChange}
          />
          <FormItemContainer>
            <ItemName></ItemName>
            <RadioFormItem
              radioItemName=""
              id="student"
              value="student"
              type="radio"
              name="identity"
              handleChange={handleLoginDataChange}
            />
            <RadioFormItem
              radioItemName=""
              id="teacher"
              value="teacher"
              type="radio"
              name="identity"
              handleChange={handleLoginDataChange}
            />
          </FormItemContainer>
          <FormItem
            itemName=""
            id="password"
            value={loginData.password}
            type="password"
            name="password"
            handleChange={handleLoginDataChange}
          />
          <Btn></Btn>
        </FormContainer>
      </Container>
    </Wrapper>
  );
}

export default LoginPage;
